import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Navigate, NavLink } from "react-router-dom";
import { Button } from "@mui/material";

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData(" ", "", 3810439479, "", "09183468975"),
  createData(" ", "", 210065948, "", "0921564213"),
  createData(" ", "", 2920654987, "", "0914164987"),
  createData(" ", "", 2870645236, "", "09183468975"),
  createData(" ", "", 3720645921, "", "09221976549"),
  createData(" ", "", 383064597, "", "09112365497"),
  createData(" ", " ", 10032654923, "", "09183468975"),
  createData(" ", "", 16413645912, "", "09143815527"),
];

export default function BasicTable() {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow
            sx={{
              "&:last-child th": {
                borderColor: "#FF0000",
              },
              backgroundColor: "#E5E7E9",
            }}
          >
            <TableCell align="center"> </TableCell>
            <TableCell align="center"> </TableCell>
            <TableCell align="center"></TableCell>
            <TableCell align="center"></TableCell>
            <TableCell align="center"></TableCell>
            <TableCell align="left"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows?.map((row, index) => (
            <TableRow
              key={row.index}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell component="th" scope="row" align="center">
                {row.name}
              </TableCell>
              <TableCell align="center">{row.calories}</TableCell>
              <TableCell align="center">{row.fat}</TableCell>
              <TableCell align="center">{row.carbs}</TableCell>
              <TableCell align="center">{row.protein}</TableCell>
              <TableCell align="right">
                <NavLink to="/patientinfo" state={rows[index]}>
                  <Button variant="contained" sx={{ color: "#0D24B8" }}>
                    
                  </Button>
                </NavLink>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
